import formCreate from './formCreate';
import baseComponent from './mixins';
import {$nt} from "./util";

const formCreateName = 'FormCreate';

/**
 * $FormCreateå°è£äºFormCreateï¼æ··å¥äºmixins
 * coreComponentä¹æ··å¥äºmixins
 */
const $FormCreate = () => ({
    name: formCreateName,
    //$FormCreateçä¸äºå¬å±æä½å°è£å°äºmixins.js
    mixins: [baseComponent],
    props: {
        rule: {
            type: Array,
            required: true,
            default: () => {
                return {}
            }
        },
        option: {
            type: Object,
            default: () => {
                return {}
            },
            required: false
        },
        //å¨index.htmlév-modelç»å®fApiï¼é»è®¤valueçå±æ§
        value: Object
    },
    render() {
        return this._fComponent.render();
    },
    beforeCreate() {
        const {rule, option} = this.$options.propsData;
        //$FormCreateå°è£äº
        const _fc = new formCreate(rule, option);

        this._fComponent = _fc;
        _fc._type = 'rule';
        _fc.beforeBoot(this);
    },
    created() {
        const _fc = this._fComponent;

        _fc.boot();
        this.$f = _fc.fCreateApi;

        //è¿éæ´æ°v-model="fApi"çå¼
        this.$emit('input', _fc.fCreateApi);
    },
    mounted() {
        const _fc = this._fComponent;

        _fc.mounted(this);

        this.$watch('rule', n => {
            _fc.reload(n);
            this.$emit('input', this.$f);
        });
        this.$watch('option', n => {
            $nt(() => {
                this._sync();
            });
        }, {deep: true});

        this.__init();
        this.$emit('input', this.$f);
    }
});

export {
    $FormCreate,
    formCreateName
};
